.top_menu > li > a {

  display: inline-block;
  color: inherit;
  text-decoration: none;
  transition: background-color 0.25s, color 0.15s; }

.top_menu {
  display: inline-block;
  list-style: none;
  padding-left: 0;
  margin: 0; }

.top_menu > li {
  display: inline-block;
  position: relative; }

.inner_menu {
  position: absolute;
  top: 100%;
  left: 0;
  list-style: none;
  padding-left: 0;
  text-align: left;
  margin: 0; }

.top_menu > li:hover > ul a {
  opacity: 1;
  height: 30px;
  padding: 10px;
  transition: height 0.3s, opacity 0.4s; }

.top_menu > li:hover > ul li {
  height: 40px;
  opacity: 1; }


.inner_menu > li {
  height: 0;
  opacity: 0;
  transition: height 0.3s, opacity 0.4s; }

.inner_menu > li > a {
  width: 100%;
  text-decoration: none;
  overflow: hidden;
  opacity: 0;
  transition: height 0.4s, opacity 0.5s; }

.top_menu > li > ul a {
  height: 0;
  margin: 0;
  padding: 0;
  transition: height 0.3s, opacity 0.4s, padding 3s linear; }

